.App {
  font-family: "Roboto", sans-serif;
  background-color: rgb(248, 248, 248);
  height: 150vh;
  color: rgb(55, 55, 55);
}

.MuiAccordion-root.Mui-expanded:first-child {
  margin: auto;
  margin-bottom: 20px;
}

.Row {
  justify-content: center;
  margin: 15px;
}

.Row .VideoPlay {
  margin-bottom: 50px;
}
@media (max-width: 900px) {
  .Row {
    flex-direction: column;

    align-items: center;
  }
}
@media (max-width: 900px) {
  .Menu {
    margin-bottom: 40px;
  }
}

.Menu {
  border: 2px solid grey;
  border-radius: 4px;
  padding: 5px;
  background-color: rgb(22, 27, 31);
  box-shadow: 0 4px 6px rgb(12, 12, 12);
}

.Titletext {
  font-size: large;
  display: inline-block;
  text-align: left;
}

.keybind .swatch {
  margin-bottom: -3px;
}

.inline {
  display: inline-block;
}

.inline-block {
  display: inline;
}

.block {
  display: block;
}

button, input[type=file] {
  background-color: #202b33;
  border: solid 1px transparent;
  border-radius: 4px;
  padding: 5px;
  color: #ffffff;
  font-size: 14px;
  margin: 5px;
  cursor: pointer;
  word-spacing: 5px;
}

.video-input {
  min-width: 600px;
}

button:hover, .button-active {
  background-color: #106ba3;
  box-shadow: 0 5px 15px rgba(145, 92, 182, .4);
}

.Stopwatch-display {
  padding: 20px 0;
  font-size: 48px;
}
.Stopwatch-text {
}
.Countdown-display {
  margin-top: 5px;
  margin-bottom: 20px;
}
.Countdown-display button {
  margin: 0 15px;
  border: solid 1px transparent;
  border-radius: 2px;
  padding: 4px 16px;
  color: #ffffff;

  font-size: 16px;
}
.Countdown-display button:hover {
  background-color: #106ba3;
}
.Countdown-label {
  font-size: 18px;
  margin-top: 5px;
  margin-bottom: 10px;
}
.Countdown-time {
  font-size: 36px;
  margin: 5px 0;
}

.GoogleChart > div {
  margin: auto;
}